$uv-info-disabled: #c4c6c9 !default;
$uv-info-light: #f4f4f5 !default;

.uv-tags {
  max-width: 100%;
  width: fit-content;
  text-align: center;
  letter-spacing: 0;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: flex;
