  justify-content: center;

  &--circle {
    border-radius: 100rpx;
  }

  &--square {
    border-radius: 4rpx;
  }

  &__icon {
    margin-right: 4rpx;
  }

  &__text {
    &--mini {
      font-size: 24rpx;
      line-height: 24rpx;
    }

    &--medium {
      font-size: 26rpx;
      line-height: 26rpx;
    }

    &--large {
      font-size: 30rpx;
      line-height: 30rpx;
    }
  }

  &--mini {
    height: 40rpx;
    line-height: 40rpx;
    padding: 0 12rpx;
    &--right {
      padding-right: 2px;
    }
  }

  &--medium {
    height: 40rpx;
    line-height: 40rpx;
    padding: 0 20rpx;
    &--right {
      padding: 0 4rpx 0 8rpx;
    }
  }

  &--large {
    height: 40rpx;
    line-height: 40rpx;
    padding: 0 20rpx;
    &--right {
      padding: 0 4rpx 0 8rpx;
    }
  }

  &--primary {
    background-color: $hubei-tag-box-bg-color;
    border-width: 1rpx;
    border-color: $hubei-tag-box-border-color;
  }

  &--primary--plain {
    border-width: 1rpx;
    border-color: $hubei-tag-box-border-color;
  }

  &--primary--plain--fill {
    background-color: $hubei-tag-box-bg-color;
  }

  &__text--primary {
    color: $hubei-tag-box-font-color;
  }

  &__text--primary--plain {
