  }

  &--warning--plain {
    border-width: 1rpx;
    border-color: $uv-warning;
  }

  &--warning--plain--fill {
    background-color: #fdf6ec;
  }

  &__text--warning {
    color: #ffffff;
  }

  &__text--warning--plain {
    color: $uv-warning;
  }

  &--success {
    background-color: $uv-success;
